<template>
  <div id="app">
    <div id="parentx">

      <div id="headernav">
        <vs-navbar v-model="activeItem" class="nabarx"  active-text-color="#1781E3">

          <img src="@/assets/apple.png" style="max-height:38px;"/>
          <vs-navbar-title style="margin:auto;">
            Äpel
          </vs-navbar-title>
          <vs-spacer></vs-spacer>
          <vs-navbar-item index="0">
            <a href="#">Home</a>
          </vs-navbar-item>
          <vs-navbar-item index="1">
            <a href="#">About Us</a>
          </vs-navbar-item>
          <vs-navbar-item index="2">
            <a href="#">Projects</a>
          </vs-navbar-item>
          <vs-navbar-item index="3">
            <a href="#">My Ideas</a>
          </vs-navbar-item>
          <vs-button vs-type="flat" vs-radius="50%" vs-icon="menu" @click="active=!active, notExpand = false" color="primary"></vs-button>
        </vs-navbar>
      </div>
      <router-view style="min-height:100vh;position:absolute;top:0px;width:100%;"></router-view>
      <vs-sidebar :reduce="reduce" :reduce-not-hover-expand="notExpand" parent="body" default-index="1"  color="primary" class="sidebarx" spacer v-model="active" position-right>

        <div class="header-sidebar" slot="header">
          <vs-avatar  size="70px" src="https://randomuser.me/api/portraits/men/85.jpg"/>
        </div>
        <vs-sidebar-group open title="App">
          <vs-sidebar-item index="1" icon="menu" @click="reduce=!reduce">
            Toggle Sidebar
          </vs-sidebar-item>
          <vs-sidebar-item index="5" icon="verified_user">
            Configurations
          </vs-sidebar-item>
          <vs-sidebar-group title="Store">
            <vs-sidebar-item index="2.1" icon="store">
              Store
            </vs-sidebar-item>
            <vs-sidebar-item index="2.2" icon="nature_people">
              Nature
            </vs-sidebar-item>
            <vs-sidebar-item index="2.3" icon="style">
              Style
            </vs-sidebar-item>
          </vs-sidebar-group>
          <vs-sidebar-item index="2" icon="gavel">
            History
          </vs-sidebar-item>
          <vs-sidebar-item index="3" icon="https">
            Security
          </vs-sidebar-item>
          <vs-sidebar-item index="4" icon="help">
            Help
          </vs-sidebar-item>
        </vs-sidebar-group>

        <vs-divider icon="person" position="left">
          User
        </vs-divider>

        <vs-sidebar-item index="6" icon="account_box">
          Profile
        </vs-sidebar-item>

        <div class="footer-sidebar" slot="footer">
          <vs-button vs-icon="settings" color="primary" vs-type="border"></vs-button>
        </div>

      </vs-sidebar>
    </div>
    <div class="footer">
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    active: false,
    notExpand: false,
    reduce: true,
    activeItem: 0
  })
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background-color:white;
  margin:0px;
  min-height:100vh;
}
.header-sidebar {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
}
.header-sidebar h4 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.header-sidebar h4 > button {
  margin-left: 10px;
}
.footer-sidebar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.footer-sidebar > button {
  border: 0px solid rgba(0,0,0,0) !important;
  border-left: 1px solid rgba(0,0,0,0.07) !important;
  border-radius: 0px !important;
}
  *{
    font-family:'K2D';
    font-weight:bold;
  }
  #headernav{
    width:90%;
    margin:auto;
    background-color:white;
    position:fixed;
    top:20px;
    border-radius:6px;
    overflow:hidden;
    height:38px;
    box-shadow: 0px 0px 300px -6px rgba(171,171,171,1);
    z-index:100;
    margin:auto;
    left:0px;
    right:0px;
  }
  #headernav li{
    height:38px;
  }
  #headernav .vs-navbar-item span{
    border-radius:2px 2px 0 0;
  }
  #headernav .vs-navbar .vs-navbar-title{
    padding: 5px !important;
  }
  .vs-sidebar{
    overflow:hidden;
  }
  .vs-component.vs-button{
    border-right:0px;
  }
  .footer{
    background-color:#1781E3;
    min-height:400px;
  }
</style>



