<template>
  <div class="hello">
    <div class="head">
      <h1 style="color:white;font-size:52px;text-align:center;padding-top:35%;width: 100%;">Ihr Verlust. Unser Dienst. Unser Gewinn. &nbsp;Ihre Niederlage</h1>
    </div>
    <div class="main">

        <div class="card">
        </div>

    </div>
  </div>
</template>

<script>import Parallax from 'vue-parallaxy'
export default {
  name: 'HelloWorld',
  components: {
    Parallax
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
  .head{
    height:100vh;
    withd:100%;
    background: url("../assets/header.jpg") no-repeat center;
    background-size:cover;
  }
  .vs-navbar-after{
    overflow:hidden;
    border-radius:50%;
  }
  .card{
    -webkit-box-shadow: 0 16px 24px 2px rgba(0,0,0,.14), 0 6px 30px 5px rgba(0,0,0,.12), 0 8px 10px -5px rgba(0,0,0,.2);
    box-shadow: 0 16px 24px 2px rgba(0,0,0,.14), 0 6px 30px 5px rgba(0,0,0,.12), 0 8px 10px -5px rgba(0,0,0,.2);
    transform: translate3d(0px, -25px, 0px);
    max-width:95%;
    margin:auto;
    border-radius:18px;
    position:relative;
    min-height:400px;
    background: white ;
    overflow:hidden;
  }
.parallax-container {
  position: relative;
}
#ljhgkjhgk {
  position:absolute;
  bottom:0;
  left:0;
  right:0;
  transform: rotate(180deg);
}
  #asdfasdf{
    position:relative;
    float:left;
    top:16px;
    width:400px;
    margin:0px;
    padding:0px;
    right:0px;
    transform: rotate(-90deg);
    left:-140px;
    top:16px;
    z-index: 1000;
  }
  #right{
    transform: rotate(90deg);
    position:relative;
    width:400px;
    right:-140px;
    top:16px;
    float:right;
    z-index: 1000;
  }
</style>



